@import '../../assets/_colors.scss';

.Header {
  padding: 16px;
  background-color: $teal;
  font-family: 'Raleway', sans-serif;
  font-size: 20px;
  border-bottom: 1px solid $lightest-teal;
  & i {
    margin-right: 16px;
  }
  & button {
    float: right;
    background-color: $light-teal;
    color: $light;
    cursor: pointer;
    border: none;
    font-family: 'Raleway', sans-serif;
    font-size: 12px;
    font-weight: 600;
    padding: 4px;
    border: 2px $light solid;

  }
}

.Topics {
  background-color: $light;
}

.TopicList {
  background-color: $light-teal;
  list-style: none;
  font-size: 16px;
}

.Topic {
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 0 16px 16px;
  border-bottom: 1px $lightest-teal solid;
  transition: 300ms;
  &:hover {
    background-color: $lightest-teal;
  }
  & i {
    margin-right: 16px;
  }
}

.TopicChoices {
  background-color: $light;
  padding: 8px;
  overflow: hidden;
}

.TopicChoice {
  display: inline-block;
  cursor: pointer;
  color: $light-teal;
  background-color: $light;
  border: 2px solid $light-teal;
  border-radius: 2px;
  font-size: 14px;
  font-weight: 500;
  margin: 4px;
  padding: 8px;
  text-decoration: none;
  transition: 300ms;
  &:hover {
    background-color: $light-teal;
    color: $light;
  }
}

.QuizLengthLabel {
  display: inline-block;
  box-sizing: border-box;
  width: 100%;
  padding: 4px 8px 12px;
  margin-bottom: 8px;
  font-size: 14px;
  color: $light-teal;
  border-bottom: 1px solid $lightest-teal;
  font-weight: 500;
}

.QuizLength {
  margin-left: 12px;
  padding: 4px;
  font-size: 14px;
  color: $light-teal;
  border-radius: 5px;
  border: 2px solid $light-teal;
  background-color: $light;
  & option {
    color: $light-teal;
  }
}

.ChevronDown {
  font-size: 24px;
  transition: 300ms;
}

.RotateChevron {
  transform: rotate(180deg);
  transition: 300ms;
}
